:root {
    --color-primario: #b62125;
    --color-complementario: #ebebeb;
  }
*{
    margin: 0;
    padding: 0%;
    font-family:'Open Sans', sans-serif;
    text-decoration:none;
}
nav{
    top: 0;
    position:sticky;
    width: 100%;
    min-height:50px;
    background-color: var(--color-primario);
    display: flex;
    flex-wrap: wrap;
    
    
}
#navInterior{
    width: 100%;
    max-width: 800px;
    height: 100%;
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: space-between;
    margin: 0px auto;
}
nav ul{
    list-style:none;
    display: flex;
    flex-wrap: wrap;
}
nav ul li {
    
    padding:10px;
    cursor: pointer;
}
nav ul li a{
    color: #f3f3f3;
    font-weight: bold;
}
#rrss{
    display:flex;
    justify-content: center;
    float:right;
    width: 20%;
}
#rrss a{
    padding: 5px;
}
.imagenRRSS{
    width: 20px;
}
.phoneNumber {
    color: var(--color-complementario);
    text-decoration-line: underline;
}
.mail{
    color: var(--color-complementario);
    text-decoration-line: underline;
}
nav ul li:hover{
    box-shadow:inset 0 -3px 0  white;
}

div#Inicio{

    width: 100%;
    height: 500px;
    padding-top: 0;
    background-image: url("/Imagenes/exteriorRestaurante.avif");
    background-repeat: no-repeat;
    background-size: 100%;
    background-position-y: 0;
    display: flex;
    justify-content: center;
    align-items: center;
}
#terrazaExterior{
    width: 100%;
    height: 100%;
    display: block;
}

 div#fotoInicio{
    width: 25%;
    height: 15%;
    display: flex;
    justify-content: center;
    align-items: center;
}
#presentacion{
    background-color: var(--color-complementario);
    width: 100%;
    height: 400px;
    border-top: 10px;
    display:flex;
    flex-direction: column;
    justify-content: center;
    align-items:center;
}
#presentacion h1{
    text-decoration: underline;
    margin:10px;
}
#presentacion p{
    margin:0 100px;
    border-top: 10px;
    font-size: 100%;

}
footer{
    background-color: #000000;
    width: 100%;
    height:auto;
    display: flex;
    justify-content: space-around;
    align-items: center;
    align-content: flex-start;
    overflow: auto;
    flex-direction: row;
    flex-wrap: wrap;
    padding-top:30px;
}
address{
    color: var(--color-complementario);
}
.footerInfo{
    height: auto;
    width: 300px;
    
}
.footerInfo>h4{
    color: var(--color-complementario);
}
.footerInfo>p{
    color: var(--color-complementario);
}

#formulario{
    height: 300px;
    width: auto;
    background-color: var(--color-complementario);
    display: flex;
    justify-content: center;
    align-items: center;
    align-content: center;
    
}
#form {
    width: 250px;
    height: 250px;
    display: flex;
    justify-content: center;
    align-items: center;
    align-content: center;
}

#menu{
    background-color: var(--color-complementario);
    height: auto;
    
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    align-content: center;

}
#mainContacto {
    width: 100%;
    height: 400px;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    background-color: var(--color-complementario);
}


.product {
    display: table;
    border-collapse: collapse;
    width: 75%;
    margin-left: 100px;
    margin-right: 100px;
  }
  
  section {
    display: table-cell;
    padding: 10px;
    border: 1px solid #9b9595;
    vertical-align: middle;
    text-align: center;
  }
  
  .image {
    width: 50px;
  }
  
  .info {
    width: 40%;
  }
  
  .features {
    width: 20%;
  }
  
  .price {
    width: 10%;
    text-align: center;
  }
  
  .image img {
    width: 100px;
    height: 100px;
  }
  
  .category {
    background-color: var(--color-primario);
  }
  .features ul li {
    list-style: none;
  }

  .alergenos {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
  }
  .alergenos img {
    width: 54px;
    height: 77px;
  }
  
  @media only screen and (max-width: 600px) {
    #navInterior {
        padding: 0 10px; /* Adjust padding for smaller screens */
    }

    nav ul {
        width: 100%;
        justify-content: space-around;
    }

    nav ul li {
        flex: 1;
        text-align: center;
        padding: 8px;
    }

    nav ul li a {
        font-size: 14px; /* Adjust font size for smaller screens */
    }

    #rrss {
        order: 3; /* Move #rrss to the bottom on smaller screens */
    }
}

@media only screen and (max-width: 400px) {
    nav ul li {
        padding: 5px;
    }

    nav ul li a {
        font-size: 12px;
    }
}